.button {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  height: auto;
  min-width: 50px;
  font-size: 14px;
  border-radius: 5px;
  box-shadow: 0 0 8px 0 #d4d4d4;
  border: none;
  color: #3f84ab;
  padding: 10px;
}

.button:focus {
  border: 0 solid white;
  outline: none;
}

.button:active {
  box-shadow: inset 1px 1px 4px rgba(0, 0, 0, 0.4);
}

.button:hover {
  background-color: #f7f7f7;
  cursor: pointer;
}

.small {
  height: 15px;
  min-width: 15px;
  width: 15px;
}

.red {
  background-color: #dc5c41;
  color: white;
}

.red:hover {
  opacity: 0.8;
  background-color: #dc5c41;
}

.green {
  background-color: #418e24;
  color: white;
}

.green:hover {
  background-color: #418e24;
  opacity: 0.8;
}

.loading {
  width: 19px;
  height: 19px;
  border: 3px solid #62ce8f;
  position: relative;
  border-right-color: #e3e3e3;
  border-radius: 50%;
  animation: loader-rotate 1s linear infinite;
  box-sizing: border-box;
}

@keyframes loader-rotate {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
